header {
    background: linear-gradient(to top right, #ee82ee, #68a5f5);
    top: 0;
    width: 100%;
    height: 6rem;
    position: sticky;
}

.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    margin: 0 2rem;
}

.logo {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.logo img {
    height: 6rem;
    width: auto;
    margin-right: 1rem;
}

nav ul {
    display: flex;
    align-items: center;
    list-style: none;
    margin-top: 1.8rem;
}


nav ul li {
    margin-right: 5rem;
    height: 30px;
}

nav ul li a {
    color: black;
    font-weight: 650;
    text-decoration: none;
    font-size: 1.3rem;
    transition: color 0.3s ease;
}

nav ul li a:hover {
    color: #ffffff;
    text-shadow: 0 0 1em #ee82ee;
    transition: color 0.3s ease;
}

.search {
    display: flex;
    align-items: center;
    margin-top: 10px;
    font-size: 1.3rem;
    font-weight: 650;
    z-index: 2;
}
  
.search {
    display: flex;
    align-items: center;
    margin-top: 10px;
    font-size: 1.3rem;
    font-weight: 650;
    z-index: 2;
    position: relative;
  }
  
.search_wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 20px;
}
  
.search_search {
    display: flex;
    align-items: center;
}
  
.search_search p {
    margin-right: 10px;
}
  
.search_results {
    width: 12rem;
    display: flex;
    flex-direction: column;
    background-color: rgba(226, 226, 226, 0.788);
    position: absolute;
    top: 70%;
    left: 32%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
  
.search_results__content {
    margin-top: 0.4rem;
    display: flex;
    font-size: small;
}

.search_results__content:hover{
    border-left: 2px solid pink;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.search_results__content img {
    width: 3rem;
}

.search_results__content_text {
    margin-left: 0.2rem;
}
  
.search input[type="search"] {
    margin-left: 20px;
    height: 1.8rem;
    width: 12rem;
}
  
.search input[type="search"]:hover {
    box-shadow: 0 0 1em #ee82ee;
}  

.activePage {
    color: #8b1bad;
}
