a {
    text-decoration: none;
    color: inherit;
}

.anime_list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 75px;
}

.anime {
    border: 1px solid black;
    padding: 10px;
    margin: 0 0 30px 75px;
    width: 17rem;
    height: 32rem;
}

.anime img {
    width: 17rem;
    height: 25rem;
}

.loading {
    margin: 0 auto;
}